<template>
    <div class="view-style" id="game-main-id">
        <div class="back-style" id="back-style-id" :style="`background-image:url(${require('@/assets/image/main_back/main_image_4_a.jpeg')})`"></div>
        <div class="surface-picture" id="surface-picture-id">
            <!-- <audio class="audio-style" :src="`${require('@/assets/audio/Zombies On Your Lawn.mp3')}`" loop id="audio" ref="audio"></audio>
            <div class="play-music-btn" id="play-music-btn-id" @click="audioEvent" :style="`background-image:url(${require('@/assets/music.svg')})`" :title="musicName"></div> -->
            <div class="play-game-btn" id="play-game-btn-id" :style="`left:${btn.left1}px;background-image:url(${require('@/assets/playgame.webp')})`" @click="newGame"></div>
            <div v-if="continueGame_show" class="continue-game-btn" id="continue-game-btn-id" :style="`left:${btn.left1}px;background-image:url(${require('@/assets/continuegame.webp')})`" @click="continueGame"></div>
            <div class="explain-style">{{message}}</div>
        </div>
    </div>
</template>

<script>
import * as TweenMax from 'gsap/all'
import _ from 'lodash'
import { encryptLong, decryptLong } from '@/utils/jsencrypt';
import components from "@/api/components.js"
// import MainPageLightSpotDiagram from '../components/MainPageLightSpotDiagram.vue';

export default {
    // components: { MainPageLightSpotDiagram },
    name:"GameView",
    data(){
        return{
            coordinate:[
                [200,450],
                ['50%','90%'],
                ['79%','50%'],
            ],
            btn:{
                left1:0
            },
            isPlaying:false,
            musicName:"Zombies On Your Lawn",
            message:"为了正常的游玩体验，建议“F11”全屏游玩，因为所有的布局都基于全屏尺寸。",
            time_back:4,
            newGame_show:false,
            continueGame_show:false,
            image:null
        }
    },
    // watch:{
    //     "$refs.audio.currentTime"(nval,oval){
    //         console.log(nval);
    //     }
    // },
    mounted(){
        // this.getArchive();
        this.clac_dom();
        this.$nextTick(()=>{
            // this.$axios.get("/assets/main_image_4_a.png",{responseType: 'blob'}).then((res)=>{
            //     var loader = new FileReader()
            //     loader.onload = (e)=>{
            //         this.image = e.target.result
            //     }
            //     loader.readAsDataURL(res.data)
            // })
            // components.gameMessage(require("@/assets/image/message/msgBack_3.webp"),"天元一气",1)
        })
    },
    methods:{
        // 按钮样式调整
        clac_dom(){
            this.getArchive()
            this.$nextTick(()=>{
                var dom1 = document.getElementById("surface-picture-id");
                var dom2 = document.getElementById("play-game-btn-id");
    
                var width1 = dom1.offsetWidth;
                var width2 = dom2.offsetWidth;
    
                this.btn.left1 = (width1 / 2) - (width2 / 2);
            })
        },
        // 播放按钮样式调整
        audioEvent(){
            if(this.isPlaying == true){
                this.$refs.audio.pause();
                this.$refs.audio.currentTime = 0
                this.isPlaying = false;
                TweenMax.gsap.to('#play-music-btn-id',0.2,{rotate:'0deg',overwrite:'auto'})
            }else{
                this.$refs.audio.play();
                this.isPlaying = true;
                TweenMax.gsap.to('#play-music-btn-id',10,{rotate:'360deg',ease:TweenMax.Power0.easeNone,repeat:-1})
            }
        },
        // 播放bgm
        palyAudio(){
            var mp3 = require("@/assets/audio/button04a.mp3")
            this.audio = new Audio();
            this.audio.src = mp3;
            this.audio.play();
        },
        // 新建存档
        async newGame(){
            document.documentElement.requestFullscreen()
            components.newgame()
            // await common.setArchive();
            // this.$router.push({name:"MainGame"});
        },
        // 继续游玩
        async continueGame(){
            // console.log(JSON.parse(decryptLong(localStorage.getItem("archive"))));
            await this.$store.dispatch('Archive',JSON.parse(decryptLong(localStorage.getItem("archive"))));
            document.documentElement.requestFullscreen()
            this.$router.push({name:"MainGame"});
        },
        // 获取存档列表
        getArchive(){
            if(!localStorage.getItem("archive")){
                return '';
            }else{
                this.continueGame_show = true
                var archive = JSON.parse(decryptLong(localStorage.getItem("archive")));
                this.$store.dispatch('Archive',archive)
            }
        },
        // 将选择后的存档放入vuex
        // async selectArchive(){
        //     await this.$store.dispatch("addArchive", this.archive)
        //     this.$router.push({name:"MainGame"})
        // },
    }
}
</script>

