    // border: 5px groove rgba(245, 245, 245, 0.5);
    // border-radius: 10px;
    text-align: center;
    line-height: 50px;
    letter-spacing: 5px;
    font-size: 25px;
    color: honeydew;
    cursor: pointer;
    font-family: "幼圆", Courier, monospace;
    transition: 0.1s all;
    // border-image-slice:124 124 124 124;
    // border-image-width:20px 20px 20px 20px;
    // border-image-outset:0px 0px 0px 0px;
    // border-image-repeat:stretch stretch;
    // border-style:solid;
    background-size: cover;
    background-position: 50% 50%;
    background-repeat: no-repeat;
    filter: drop-shadow(2px 2px 1px #0000007c);
}
// .continue-game-btn:active{
//     background-color: rgba(240, 255, 240, 0.3);
// }
.continue-game-btn:hover{
    font-size: 30px;
    // text-shadow:2px 2px #0000007c;
    // box-shadow: 5px 5px 5px 0px #0000007c;
    filter: drop-shadow(2px 2px 1px #000000);
}
.play-music-btn{
    position: absolute;
    z-index: 2;
    top: 10px;
    right: 10px;
    width: 50px;
    height: 50px;
    // border: 5px groove rgba(245, 245, 245, 0.5);
    // border-radius: 10px;
    // text-align: center;
    // line-height: 50px;
    // letter-spacing: 5px;
    // font-size: 30px;
    // color: honeydew;
    cursor: pointer;
    // font-family: "Ink Free", Courier, monospace;
    // transition: 0.2s all;
    background-size: cover;
    background-position: 50% 50%;
    background-repeat: no-repeat;
}
